    width: 500px;
  }
  .content1 img {
    transition: 0.5s;
  }
  .content1 img:hover {
    opacity: 0.5;
  }
  
  .content2 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .content3 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  .content3 img {
    transition: 0.5s;
    width: 1000px;
  }
  .content3 img:hover {
    opacity: 0.5;
    width: 1000px;
  }



